<template>
  <div class="row">
    <div class="col-xl-4 col-lg-5 col-md-6">
      <user-card @showComponent="showComponent"></user-card>
    </div>
    <div class="col-xl-8 col-lg-7 col-md-6">
      <edit-profile-form v-show="showProfile"></edit-profile-form>
      <div v-show="showCategory">카테고리 수정</div>
    </div>
  </div>
</template>
<script>
import EditProfileForm from "./UserProfile/EditProfileForm.vue";
import UserCard from "./UserProfile/UserCard.vue";
export default {
  components: {
    EditProfileForm,
    UserCard,
  },
  data() {
    return {
      showProfile: false,
      showCategory: false,
    };
  },
  methods: {
    showComponent(arg) {
      if (arg == "showProfile") {
        this.showProfile = true;
        this.showCategory = false;
      } else {
        this.showProfile = false;
        this.showCategory = true;
      }
    },
  },
};
</script>
