<template>
  <card class="card-user">
    <div slot="image">
      <img src="@/assets/img/background.jpg" alt="..." />
    </div>
    <div>
      <div class="author">
        <img
          class="avatar border-white"
          src="@/assets/img/faces/face-0.jpg"
          alt="..."
        />
        <h4 class="title">
          김예솔
          <br />
          <a href="#">
            <small>1210yesol@gmail</small>
          </a>
        </h4>
      </div>
      <p class="description text-center"></p>
    </div>
    <hr />
    <div class="text-center">
      <div class="row">
        <div
          v-for="(info, index) in details"
          :key="index"
          :class="getClasses(index)"
        >
          <a href="#" class="setting-user-button" @click="clickEvent(index)">
            <h5>
              {{ info.title }}
              <br />
              <small>{{ info.subTitle }}</small>
            </h5>
          </a>
        </div>
      </div>
    </div>
  </card>
</template>
<script>
export default {
  data() {
    return {
      details: [
        {
          title: "",
          subTitle: "내정보",
        },
        {
          title: "",
          subTitle: "카테고리 편집",
        },
      ],
    };
  },
  methods: {
    getClasses(index) {
      var remainder = index % 3;
      if (remainder === 0) {
        return "col-lg-5 offset-lg-1";
      } else {
        return "col-lg-5";
      }
    },
    clickEvent(index) {
      if (index == 0) {
        this.$emit("showComponent", "showProfile");
      } else {
        this.$emit("showComponent", "showCategory");
      }
    },
  },
};
</script>
